import React from 'react';
import machinelearning from "../machinelearning.png";
import webdevelopment from "../webdevelopment.png";
import datascience from "../datascience.png";

function Interests() {
    return (
        <div>
             <h5><u>INTERESTS</u></h5>
        <table class="table table-borderless">
        <tbody>
            <tr>
            <td><img  src = {machinelearning} height="25px" alt="Not Found" style={{marginBottom: "10px"}}/></td>
            <td><h6 style={{display: "inline"}}>Machine Learning</h6></td>
            </tr>
            <tr>
                <td><img  src = {webdevelopment} height="25px" alt="Not Found" style={{marginBottom: "10px"}}/></td>
            <td><h6 style={{display: "inline"}}>Web Development</h6></td>
            </tr>
            <tr>
                <td><img  src = {datascience} height="25px" alt="Not Found" style={{marginBottom: "10px"}}/></td>
            <td><h6 style={{display: "inline"}}>Data Science</h6></td>
            </tr>
        </tbody>
        </table>
        </div>
    )
}

export default Interests;